.App-header {
  position: fixed;
  width: 100%;
  background-color: $header-color;
  height: 7vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  top: 0;
  z-index: 1;
}

.App-link {
  color: $link-font;
}





.errorMessage {
  border: red 1px solid;
  background-color: #FAE2E2;
  padding: 10px;
}

nav {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  padding: 10px;
}

.nav-section {
  // min-width: 250px;
  display: flex;
  justify-content: space-around;
}

a {
  text-decoration: none;
}

.nav-section {
  button {
    background-color: none;
    border: none;
    font-weight: bold;
    padding: 2px;
    text-decoration: none;
    font-size: inherit;
  }

}
.right{
  margin-right: 2vw;
}
.left{
  display: flex;
  margin-left: 2vw;
}
nav button,
nav a {
  color: $link-font;
  margin: 10px;
}

nav button:hover,
nav a:hover {
  background-color:$hover;
}

nav button:visited,
nav a:visited {
  background-color: $hover;
}

nav button:hover {
  cursor: pointer;
}
main{
  margin: 0px;
}